@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  background-image: url("img/cityBackground.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: inherit;
  background-attachment: fixed;
}

h1, h4 {
  color: #FDF5DC;
}

.link {
  color: #bde0fe;
  font-size: 17px;
}

.link:hover {
  text-shadow: 0 0 6px #4285f4;
}

.WelcomeScreen {
  position: fixed;
  width: 95%;
  max-width: 1000px;
  height: 100%;
  top: 0;
  background: #2C3D5D;
  float: left;
  padding-top: 19%;
  display: block;
  margin-left: center;
  margin-right: center;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 200px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
  background: #1669f2;
}

.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}

.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
  background-color: #4285f4;
  border: none;
  text-align: center;
}